@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  display: table;
  content: '';
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
}

[dir='rtl'] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', 'Arial', sans-serif;
  font-size: 16px;
  line-height: 30px;
  color: #eee;
  background: #444;
}

* {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

p {
  margin: 0;
  padding: 0;
}

.hidden {
  display: none;
}

.container {
  margin: 0 auto;
  max-width: 1200px;
}

.sandwitch {
  position: absolute;
  top: 18px;
  left: 30px;
  display: none;
  width: 23px;
  height: 14px;
  cursor: pointer;
  z-index: 100;
}

@media screen and (max-width: 992px) {
  .sandwitch {
    display: block;
  }
}

.sandwitch--active .sandwitch__line--top {
  top: 6px;
  transform: rotate(45deg);
}

.sandwitch--active .sandwitch__line--middle {
  opacity: 0;
}

.sandwitch--active .sandwitch__line--bottom {
  top: 6px;
  transform: rotate(-45deg);
}

.sandwitch--active .sandwitch__line {
  background: #444;
}

.sandwitch__line {
  position: absolute;
  left: 0;
  display: block;
  width: 100%;
  height: 2px;
  transition: all linear 0.1s;
  background-color: #fff;
}

.sandwitch__line--top {
  top: 0;
}

.sandwitch__line--middle {
  top: 6px;
}

.sandwitch__line--bottom {
  top: 12px;
}

.main-header-top {
  height: 50px;
  background: linear-gradient(to bottom, #555, #666);
}

.main-header .container {
  position: relative;
}

.main-menu {
  display: flex;
  justify-content: flex-end;
  width: 100%;
}

@media screen and (max-width: 992px) {
  .main-menu {
    display: none;
  }
}

.main-menu.active {
  position: fixed;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background: #eee;
}

.main-menu.active li {
  border-right: none;
}

.main-menu.active li > a {
  color: #333;
}

.main-menu.active li a {
  padding: 5px 0;
  text-align: center;
  width: 200px;
}

.main-menu > li {
  position: relative;
  border-right: 1px solid #777;
}

.main-menu > li:last-child {
  border-right: none;
}

.main-menu a {
  display: block;
  padding: 10px 15px;
  text-decoration: none;
  color: #eee;
  transition: all .3s;
}

@media screen and (max-width: 992px) {
  .main-menu a {
    border-bottom: 1px solid #ddd;
  }
}

.main-menu a:hover, .main-menu a.active {
  background: #3fbded7a;
}

.main-menu .sub-menu {
  position: absolute;
  top: 50px;
  left: 0;
  display: none;
  width: 200px;
  background: #666;
}

@media screen and (max-width: 992px) {
  .main-menu .sub-menu {
    position: static;
    width: auto;
    background: rgba(145, 213, 237, 0.48);
  }
}

.main-menu .sub-menu li:last-child a {
  border-bottom: none;
}

.main-menu .sub-menu a {
  border-bottom: 1px solid #777;
}

@media screen and (max-width: 992px) {
  .main-menu .sub-menu a {
    border-bottom: 1px solid #c3c8c8;
  }
}

.img-zoom {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  height: 300px;
  overflow: hidden;
  cursor: pointer;
}

.img-zoom:hover img {
  transform: scale(1.2) rotate(-10deg);
  opacity: 1;
}

.img-zoom img {
  width: 100%;
  opacity: .8;
  transition: all .5s;
}

.flipcard {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 300px;
  height: 450px;
  perspective: 1000px;
  transform-style: preserve-3d;
  border-radius: 8px;
  cursor: pointer;
}

.flipcard:hover .flipcard__front {
  transform: rotateY(-180deg);
}

.flipcard:hover .flipcard__back {
  transform: rotateY(0);
}

.flipcard__front {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("../i/flipcard-front.jpg") center no-repeat;
  backface-visibility: hidden;
  background-size: cover;
  border-radius: 8px;
  transform-style: preserve-3d;
  overflow: hidden;
  z-index: 100;
  transition: all .6s ease;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.3);
}

.flipcard__back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("../i/flipcard-back.jpg") center no-repeat;
  background-size: cover;
  backface-visibility: hidden;
  border-radius: 8px;
  transform: rotateY(180deg);
  transform-style: preserve-3d;
  overflow: hidden;
  z-index: 99;
  transition: all .6s ease;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.3);
}

.city-wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100vh;
}

.city__bg {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 100%;
  background: url("../i/city/1.jpg") no-repeat center;
  background-size: cover;
  z-index: 2;
}

.city__title {
  font-family: 'Arial', sans-serif;
  font-size: 60px;
  text-transform: uppercase;
  color: #fff;
}

.city__overlay {
  position: relative;
  width: 2%;
  height: 100%;
  background: url("../i/city/2.jpg") no-repeat center;
  background-attachment: fixed;
  z-index: 3;
  transition: 1s ease;
}

.city__overlay:hover {
  transition: 0s ease;
  opacity: 0;
}

.fullscreen-menu {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 100vh;
  z-index: 1;
  background: url("../i/fullscreen-menu/fullscreen-menu-1.jpg") no-repeat center;
  transition: background-image 0.3s ease-in-out;
}

.fullscreen-menu::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: darkblue;
  opacity: 0.3;
}

.fullscreen-menu__item {
  position: relative;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  border-right: 3px solid #9F658E;
  cursor: pointer;
}

.fullscreen-menu__item:hover::before {
  opacity: 0.6;
}

.fullscreen-menu__item:hover .fullscreen-menu__content {
  opacity: 1;
}

.fullscreen-menu__item:hover .fullscreen-menu__icon {
  color: #ffd700;
}

.fullscreen-menu__item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #9F658E;
  opacity: 0.3;
  z-index: -1;
  transition: all 0.3s;
}

.gallery-3d {
  margin: 380px auto 0;
  max-width: 200px;
  height: 200px;
  transform-style: preserve-3d;
  animation: animateGallery 20s linear infinite;
}

.gallery-3d span {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform-origin: center;
  transform-style: preserve-3d;
  transform: rotateX(calc(var(--i) * 45deg)) translateZ(300px);
}

.gallery-3d img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@keyframes animateGallery {
  0% {
    transform: perspective(1000px) rotateX(0deg) rotateY(45deg);
  }
  100% {
    transform: perspective(1000px) rotateX(360deg) rotateY(45deg);
  }
}

.page-404 {
  min-height: 100vh;
  background: linear-gradient(0deg, #fff, #03a9f4);
}

.page-404__sky {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60vh;
}

.page-404__title {
  font-size: 136px;
  text-shadow: 15px 15px 0 rgba(0, 0, 0, 0.1);
  color: white;
}

.page-404__title span {
  display: inline-block;
  animation: animate 2s ease-in-out infinite;
}

.page-404__title span:nth-child(even) {
  animation-delay: 1s;
}

.page-404__field {
  padding: 100px;
  height: 40vh;
  text-align: center;
  box-shadow: inset 0 20px 10px #51680c;
  background-color: #6e2308;
}

.page-404__field-title {
  margin-bottom: 30px;
}

.page-404__link {
  display: inline-block;
  padding: 15px 25px;
  font-size: 24px;
  font-weight: bold;
  text-decoration: none;
  text-transform: uppercase;
  color: black;
  background-color: white;
  border-radius: 10px;
  border: 1px solid white;
  transition: all .3s;
}

.page-404__link:hover {
  color: white;
  background-color: transparent;
}

.page-404__plane {
  position: absolute;
  top: 26%;
  left: 16%;
}

.page-404__grass {
  position: absolute;
  bottom: -30px;
  width: 100%;
  height: 40px;
  background-image: url("../i/page-404/grass.png");
  background-repeat: repeat-x;
  animation: animateGrass .2s linear infinite;
}

@keyframes animate {
  0%, 100% {
    transform: translateY(-50px);
  }
  50% {
    transform: translateY(50px);
  }
}

@keyframes animateGrass {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: -94px 0;
  }
}
